import { useSelector } from "react-redux";
import ProductItem from "../../components/ProductItem"
import useCurrencyRate from '../../hook/useCurrencyRate';


export default function RecentProduct({products}){
    const { themeMode } = useSelector((state) => state.setting);
    const currencyRate = useCurrencyRate();
    const borderColor = ((themeMode === 'light') ? 'border-stone-300' : 'border-gray-800');

    return(
        <div className="grid sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-2 sm:gap-4">

            {products.map((product,index)=>(
                <ProductItem
                product={product}
               
                currencyRate={currencyRate}
                key={index}
                index={index}
                layout={'grid'}
                borderColor={borderColor}
            />
            ))}
        </div>
    )
}