import Slider from 'react-slick';
import Image from '../../components/Image';
import SliderWrapper from "../../components/SlickSliderStyle";
import { ASSETS_URL } from '../../utils/API';

const SLICK_SETTING = {
    dots: true,
    infinite: true,
    autoplay: true,
    arrows:false,
    autoplaySpeed: 5000,
    speed: 500,
    slidesToShow: 1,
    initialSlide: 1,
    responsive: [
        {
            breakpoint: 1024,
            settings: {
                slidesToShow: 1,
                infinite: true,
                dots: true
            }
        },
        {
            breakpoint: 600,
            settings: {
                slidesToShow: 1,
                initialSlide: 1
            }
        },
        {
            breakpoint: 480,
            settings: {
                slidesToShow: 1,
            }
        }
    ],
    appendDots: dots => <ul >{dots}</ul>,
    customPaging: i => (
        <div className="ft-slick__dots--custom">
            <div className="loading" />
        </div>
    )
}

export default function SliderBanner({ banners }) {
    return (
        <div className=''>
            <SliderWrapper>
                <Slider {...SLICK_SETTING}>
                    {banners.map((banner, index) => (
                        <div key={index} className="h-full w-full over-flow-hidden">
                            <img src={`${ASSETS_URL.ezo}../${banner.image}`} alt='' className='banner-image w-full min-h-[200px]' />
                        </div>
                    ))}

                </Slider>
            </SliderWrapper>
        </div>
    )
}