import { t } from 'i18next';
import { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import Page from '../components/Page';
import CustmerFeedback from '../sections/home/CustomerFeedback';
import RecentProduct from '../sections/home/RecentProduct';
import SliderBanner from '../sections/home/SliderBanner';
import { API_CLIENT, SEND_GET_REQUEST, SEND_POST_REQUEST } from '../utils/API';

export default function Home() {
    const [banners, setBanners] = useState([]);
    const [siteFeedback, setSiteFeedback] = useState([])
    const { hotProducts } = useSelector((state) => state.shopping);
    useEffect(() => {
        SEND_GET_REQUEST(API_CLIENT.product.getBannerImages).then(res => {
            if (res.status === 200) {
                console.log(res.data);
                setBanners(res.data);
            }
        }).catch(err => {

        })
        SEND_GET_REQUEST(API_CLIENT.user.getSiteFeedback).then(res => {
            if (res.status === 200) {
                console.log(res.data);
                setSiteFeedback(res.data);
            }
        }).catch(err => {

        })
    }, []);
    return (
        <Page title="Home">
            <div className='w-full mt-20 py-2 '>
                <SliderBanner banners={banners} />

                <div className='w-full mt-20 container mb-4'>
                    <div className='w-full p-2 flex justify-center mb-2'>
                        <h3 className='border-b-2 text-3xl px-4'>{t('words.recent-products')}</h3>
                    </div>

                    <RecentProduct products={hotProducts} />

                    <div className='w-full flex justify-center py-10'>
                        <button className="btn uppercase btn-ghost"><Link to='/shopping'>View More Products</Link></button>
                    </div>
                    
                    <div className='bg-base-300 rounded-lg p-4'>
                        <h3 className="text-2xl text-center font-black py-4 border-base-100 border-b-2 mb-4">Feedback from customers</h3>
                        <CustmerFeedback feedbacks={siteFeedback} />
                    </div>
                </div>
            </div>

        </Page>
    )
}