import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import ProductItem from "../../components/ProductItem"
import Rating from "../../components/Ratings";
import TextMaxLine from "../../components/TextMaxLine";
import useCurrencyRate from '../../hook/useCurrencyRate';
import { ASSETS_URL } from "../../utils/API";


export default function CustmerFeedback({ feedbacks }) {
    const { themeMode } = useSelector((state) => state.setting);

    const borderColor = ((themeMode === 'light') ? 'border-stone-300' : 'border-gray-800');

    return (
        <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-2 sm:gap-4 pt-10">
            
            {feedbacks.map((feedback, index) => (
                <div className="flex flex-col bg-base-100 p-4 gap-4 relative pt-12 rounded-lg mb-12" key={index} >
                    <div className="rounded-full h-20 w-20 border-accent overflow-hidden border-2 absolute -top-10">
                        <img src={`${ASSETS_URL.root}${feedback?.t_client?.image}`} alt="" />
                    </div>
                    <div className="flex w-full justify-between items-center">
                        <div>
                            <h3 className="text-xl font-black">{feedback?.t_client?.firstname}{feedback?.t_client?.lastname}</h3>
                            <h6>{feedback?.t_client?.phone}</h6>
                        </div>
                        
                        <Rating readOnly value={feedback?.rating} />
                    </div>

                    <TextMaxLine maxLine={4} text = {feedback?.feedback} />

                </div>
            ))}
        </div>
    )
}